.card-album:hover {
  transform: scale(1.02);
}
.card-text {
  margin-bottom: 0;
}

.link-more {
  text-decoration: underline;
}

.link-more:hover {
  text-decoration: underline;
  color: #48c2fc;
}

#tooltip {
  position: absolute;
  left: 0;
  top: 4rem;
  font-style: normal;
  font-weight: 800;
  letter-spacing: 1px;
  line-height: 1.42857143;
  text-align: left;
  text-align: start;
  text-shadow: none;
  text-transform: none;
  white-space: normal;
  word-break: normal;
  word-spacing: normal;
  word-wrap: normal;
  font-size: 12px;
  outline: none;
  display: inline-block;
  max-width: 500px;
  z-index: 10;
}

/* #tooltip.right {
  margin-left: 3.5vw;
} */

#tooltip .tooltip-inner {
  padding: 3px 8px;
  width: 200px;
  color: #fff;
  text-align: center;
  background-color: #828fa9;
  border-radius: 4px;

  transition: opacity 200ms ease-out;
}

#tooltip.on {
  display: block;
}

#tooltip.off {
  display: none;
}

#tooltip .tooltip-arrow {
  top: 50%;
  left: 0;
  margin-top: -5px;
  border-width: 5px 5px 5px 0;
  border-right-color: #01c8ff;
}

